<template>
    <div>
    <div v-if="imageload" style="position: absolute;top:0px;z-index:1000;">
        <div class="bg-dark text-white" style="width:100vw;">
            <div class="row ">
                <div class="col-1 bg-blue-400 text-center animated slideInLeft" style="height:25px;" v-for="(item, index) in loadnya" :key="index+'loadnya'">
                    <span v-if="index==0">
                        Loading
                    </span>
                </div>
            </div>
        </div>
    </div>
    <div v-if="modal" style="height:100vh;width:100vw;background:transparent;position:absolute;z-index:100;" @click="modal=false">
    </div>
    <div v-if="modal" style="position: fixed;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:1000;">
        <div style="height:100vh;" class="d-flex justify-content-center align-items-center">
            <div style="width:100%">
                <div class="row justify-content-center">
                    <div class="col-md-12">
                        <div class="bg-white p-3 rounded-lg shadow" style="height:90vh;overflow:scroll;">
                            <button type="button" @click="modal=false;btn = 'tambah';vdata = {size:250};"
                             class="btn btn-sm btn-danger  float-right">x</button>
                             <a :href="`/#/office/360view?idgambar=${$route.query.idgambar}`" target="__blank">
                                 <button type="button"  class="btn btn-sm btn-dark  ">
                                 Preview </button>
                             </a>
                            <hr>
                            <form  @submit.prevent="submit">
                            <div class="row justify-content-center">
                                <div class="col-sm-9 p-2" style="font-size:12px;">
                                <div class="shadow p-3 rounded-lg">
                                <!-- INPUT -->
                                <div class="sm-form">
                                    <input
                                    type="text"
                                    id="id"
                                    name="id"
                                    class="form-control form-control-sm"
                                    placeholder="id"
                                    v-model="vdata.id"
                                    disabled
                                    />
                                </div>
                                <hr class="mt-2 mb-2">
                                <p class="font-semibold text-lg p-3">Hotspot untuk : {{$route.query.title}}</p>
                                            <select class='form-control' v-model="vdata.type">
                                                <option>Route</option>
                                                <option>Text</option>
                                                <option>Photo</option>
                                                <option>Video</option>
                                            </select>
                                            <hr class="mt-2 mb-2">
                                            <p class="font-semibold">Hotspot Point : </p>
                                            <div class="row">
                                                <div class="col-6">
                                                    <div class="sm-form ">
                                                        <label for="title">Title</label>
                                                        <input type="text" id="title" name="title" class="form-control form-control-sm" placeholder="title" v-model="vdata.title" >
                                                    </div>
                                                    <div class="sm-form ">
                                                        <label for="hotspot">hotspot</label>
                                                        <input type="text" id="hotspot" name="hotspot" class="form-control form-control-sm" placeholder="hotspot point" v-model="vdata.hotspot" >
                                                    </div>
                                                    <div class="sm-form ">
                                                        <label for="size">Size</label>
                                                        <input type="text" id="size" name="size" class="form-control form-control-sm" placeholder="size" v-model="vdata.size" >
                                                    </div>
                                                </div>
                                                <div class="col-6 text-center">
                                                <br>
                                                    <a 
                                                        target="__blank"
                                                        :href="`/#/zadmin/kategori/360data/getPoint?idgambar=${$route.query.idgambar}&idpeta=${$route.query.idpeta}`"> 
                                                        <button type="button" 
                                                        class="btn btn-sm btn-dark  ">View <img src="https://upload.egov.balangankab.go.id/svg/image.svg" 
                                                            class="d-inline" style="height:15px;width:15px;filter: brightness(0) invert(1);" alt=""></button>
                                                        </a>
                                                </div>
                                                <div class="col-12">
                                                
                                                </div>
                                            </div>
                                            <hr class="mt-2 mb-2">
                                            <div v-if="vdata.type=='Route'">
                                                <p class="font-semibold">Route : </p>
                                                <v-select :options="listphoto" label="title" v-model="vdata.idroute" :reduce="e => e.id"></v-select>
                                                <label for="initialRoute">Intial Route Koordinat</label>
                                                <div class="row">
                                                    <div class="col-6">
                                                        <div class="sm-form ">
                                                            <input type="text" id="initialRoute" name="initialRoute" class="form-control form-control-sm" placeholder="initialRoute point" v-model="vdata.initialRoute" >
                                                        </div>
                                                    </div>
                                                    <div class="col-6">
                                                        <a 
                                                        target="__blank"
                                                        :href="`/#/zadmin/kategori/360data/getPoint?idgambar=${vdata.idroute}&idpeta=${$route.query.idpeta}`"> 
                                                        <button type="button" 
                                                        class="btn btn-sm btn-dark  ">View <img src="https://upload.egov.balangankab.go.id/svg/image.svg" 
                                                            class="d-inline" style="height:15px;width:15px;filter: brightness(0) invert(1);" alt=""></button>
                                                        </a>
                                                    </div>
                                                </div>
                                            </div>
                                            <div v-else-if="vdata.type=='Text'">
                                                <p class="font-semibold">Text : </p>
                                                <div class="sm-form">
                                                    <textarea type="text" id="text" name="text" rows="2" placeholder="text..." class="form-control md-textarea" v-model="vdata.text" ></textarea>
                                                </div>
                                            </div>
                                            <div v-else-if="vdata.type=='Photo'">
                                                <p class="font-semibold">Photo : </p>
                                                <div class="sm-form ">
                                                    <label for="gambar">Your gambar</label>
                                                    <input type="file" id="filenya" name="gambar" class="form-control form-control-sm" placeholder="gambar" @change="upload" >
                                                    <input type="text" id="gambar" name="gambar" class="form-control form-control-sm" placeholder="gambar" v-model="vdata.gambar" >
                                                </div>
                                            </div>
                                            <div v-else-if="vdata.type=='Video'">
                                                <p class="font-semibold">Text : </p>
                                                <div class="sm-form ">
                                                    <label for="youtube">Youtube</label>
                                                    <input type="text" id="youtube" name="youtube" class="form-control form-control-sm" placeholder="youtube" v-model="vdata.youtube" >
                                                </div>
                                            </div>
                                            <hr class="mt-2 mb-2">
                                            <div class="row">
                                            <div class="col-6">
                                                <p class="font-semibold">Icon : </p>
                                                <select class='form-control' v-model="vdata.icon">
                                                    <option value="Info">Info</option>
                                                    <option value="Arrow">Arrow</option>
                                                    <option value="Custom">Custom</option>
                                                </select>
                                                <div class="sm-form ">
                                                    <label for="iconnya">Your iconnya</label>
                                                    <input type="file" id="iconnya" name="iconnya" class="form-control form-control-sm" placeholder="iconnya" @change="upload2">
                                                    <input type="text" id="iconnya" name="iconnya" class="form-control form-control-sm" placeholder="iconnya" v-model="vdata.iconnya" >
                                                </div>
                                            </div>
                                            <div class="col-6">
                                                
                                                <p class="font-semibold">Detail : </p>
                                                <div class="sm-form">
                                                    <textarea type="text" id="detail" name="detail" rows="2" placeholder="detail..." class="form-control md-textarea" v-model="vdata.detail"
                                                ></textarea>
                                            </div>
                                            </div>
                                            <hr class="mt-2 mb-2">
                                    </div>
                                <hr>
                                <!-- END INPUT -->
                                <!-- BUTTON PART -->
                                <div class="mt-3 p-3">
                                    <button
                                    type="submit"
                                    class="btn btn-sm"
                                    :class="
                                        btn == 'tambah'
                                        ? 'btn-primary'
                                        : btn == 'update'
                                        ? 'btn-success'
                                        : 'btn-danger'
                                    "
                                    >
                                    <span class="typcn typcn-info"></span> Proses  {{ btn }}
                                    </button>
                                    <div class="float-right">
                                    <button
                                        type="button"
                                        v-show="btn!='tambah'"
                                        @click="
                                        btn = 'tambah';
                                        vdata = {size:250};
                                        "
                                        class="btn btn-sm btn-dark"
                                    >
                                        <span class="typcn typcn-edit"></span> Tambah
                                    </button>
                                    <button
                                        type="button"
                                        @click="btn = 'delete'"
                                        v-show="btn!='tambah'"
                                        class="ml-3 btn btn-sm btn-danger"
                                    >
                                        <span class="typcn typcn-delete"></span> Delete
                                    </button>
                                    </div>
                                </div>
                                </div>
                                <!-- END BUTTON PART -->
                                </div>
                                <div class="col-sm-9 p-2" style="font-size:12px;">
                                <div class="shadow rounded-lg">
                                    <!-- TABLE PART -->
                                    <div v-if="datanya.length>0" class="p-2">
                                    <div class="text-right">
                                        <vue-json-excel :data="datanya">
                                        <button type="button" class="btn btn-sm btn-dark">
                                            Export <span class="typcn typcn-chart-area"></span>
                                        </button>
                                        </vue-json-excel>
                                    </div>
                                        <Btables :tables="datanya"  :cari="['']" :hide="['gambar','point','idpeta','detail','text','initialRoute','idgambar','idroute','iconnya']" @selected="ambil" ref="btable" />
                                    </div>
                                    <!-- END TABLE PART -->
                                </div>
                                </div>
                            </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div v-if="mapready">
        <div id="container" style="height:100vh;width:100vw;"></div>
    </div>
    </div>
</template>
<script>
import autophp from "@/plugins/autophp_laravel.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let urlnya=localStorage.getItem('urlnya');
let sdb = new autophp();
export default{
    components:{
        Btables
    },
    data(){
        return{
            datanya:[],
            photo:[],
            listphoto:[],
            listHotspot:[],
            imageload:true,
            loadnya:[],
            modal:false,
            show:false,
            ready:false,
            mapready:false,
            gambarUtama:'',
            btn:'tambah',
             vdata: {
                size:250
            },
            btn: "tambah",
            info:{},
            point:null
        }
    },
    methods: {
         async submit() {
            let that=this
            this.vdata.idpeta=this.$route.query.id
            this.vdata.idgambar=this.$route.query.idgambar
            if (confirm("Apakah yakin proses dilanjutkan ?")) {
                if (this.btn == "tambah") {
                delete this.vdata.id;
                await sdb
                    .collection("app_gis_360data_hotspot",true,urlnya)
                    .doc()
                    .set(this.vdata)
                    that.getDataAsli();
                    that.mapready=false;
                    that.inits();
                } else if (this.btn == "update") {
                await sdb
                    .collection("app_gis_360data_hotspot",true,urlnya)
                    .doc()
                    .set(this.vdata)
                    that.getDataAsli();
                    that.mapready=false;
                    that.inits();
                } else if (this.btn == "delete") {
                await sdb
                    .collection("app_gis_360data_hotspot",true,urlnya)
                    .doc(this.vdata.id)
                    .delete()
                    that.getDataAsli();
                    that.mapready=false;
                    that.inits();
                }
            }
        },
        upload(){
            let file = document.querySelector("#filenya"); // berikan id pada input file
            sdb.collection('tbuser',false,urlnya).upload(file,false,true) // file,compress,scramble
            .then(res=>{
                console.log(res)
                this.vdata.gambar=res;
                this.$forceUpdate();
            })
        },
        upload2(){
            let file = document.querySelector("#iconnya"); // berikan id pada input file
            sdb.collection('tbuser',false,urlnya).upload(file,false,true) // file,compress,scramble
            .then(res=>{
                console.log(res)
                this.vdata.iconnya=res;
                this.$forceUpdate();
            })
        },
        getDataAsli(){
            sdb.collection("app_gis_360data_hotspot",false,urlnya).doc().select(`select * from app_gis_360data_hotspot where idgambar='${this.$route.query.idgambar}'`).then(res => {
                this.datanya=res;
                this.$forceUpdate();
            });
        },
        kopy(){
            navigator.clipboard.writeText(this.point).then(function() {
             sdb.alert('Copying Point ke Clipboard!','bg-green-400');
            }, function(err) {
             sdb.alert('Could not copy text: ');
            });
        },
        getDatanya(){
            this.ready=false;
            sdb.collection("app_gis_360data_hotspot",false,urlnya).doc().select(`select * from app_gis_360data_hotspot`).then(res => {
                this.datanya=res;
                this.ready=true;
                this.$forceUpdate();
            });
        },
        getData(){
            sdb.collection("app_gis_360data",false,urlnya).doc().select(`select * from app_gis_360data where id='${this.$route.query.idgambar}'`).then(res => {
                console.log(res);
                this.photo=res;
                this.$forceUpdate();
            });
        },
        getListPhoto(){
            sdb.collection("app_gis_360data",false,urlnya).doc().select(`select * from app_gis_360data where idpeta='${this.$route.query.idpeta}'`).then(res => {
                console.log('listphoto',res);
                this.listphoto=res;
                this.$forceUpdate();
            });
        },
         ambil(data) {
            this.vdata = data;
            this.btn = "update";
            this.$forceUpdate();
        },
        inits(){
            let that=this;
            this.mapready=true;
            sdb.collection("app_gis_360data_hotspot",false,urlnya).doc().select(`select tb1.type,tb1.initialRoute,tb1.icon,tb1.detail,tb1.text,tb1.gambar,tb1.idgambar,tb1.youtube,tb1.point,tb1.hotspot,tb1.idroute,tb1.idpeta,tb2.gambar as gambarnya ,tb1.iconnya,tb1.title,tb1.size
                from app_gis_360data_hotspot tb1 left join app_gis_360data tb2 on tb2.id=tb1.idgambar where tb1.idgambar='${this.$route.query.idgambar}'`).then(res => {
                that.listHotspot=res;
                console.log('listhotspot',res)
                setTimeout(() => {
                    let that=this;
                    var panorama, panorama2, viewer, container, infospot;
                        container = document.querySelector( '#container' );
                        panorama = new PANOLENS.ImagePanorama( this.photo[0].gambar );
                        panorama.addEventListener( 'enter-fade-start', function(){ //set posisi pertama di gambar
                            viewer.tweenControlCenter( new THREE.Vector3(3800.69, -1119.69, 5000.00), 0 );
                        });
                         panorama.addEventListener('progress', function(e){
                            that.imageload=true;
                            that.loadnya.push('1')
                            that.$forceUpdate();
                            console.log('image progress')
                        });
                        panorama.addEventListener('load', function(e){
                            that.imageload=false;
                            that.loadnya=[]
                            that.$forceUpdate();
                        });
                        panorama.addEventListener("click", function(e){
                            that.show=true;
                            if (e.intersects.length > 0) return;
                            const a = viewer.raycaster.intersectObject(viewer.panorama, true)[0].point;
                            // that.point=JSON.stringify(a)
                            navigator.clipboard.writeText(JSON.stringify(a)).then(function() {
                                sdb.alert('Copy berhasil, Paste ke initial Route','bg-green-400');
                                that.$store.state.data.point=JSON.stringify(a);
                                that.modal=true;
                            }, function(err) {
                                sdb.alert('Could not copy text: ');
                            });                   
                            
                            that.$forceUpdate();
                        });
                        viewer = new PANOLENS.Viewer( { 
                            container: container, 
                            controlBar: true,  
                            viewIndicator: true,  
                            output: 'console', 
                            indicatorSize: 30,   
                            autoHideInfospot:false 
                            // autoRotate: true, 
                            // autoRotateSpeed: 1, 
                            // autoRotateActivationDuration: 5000
                        } );
                        var infospots=[];
                        that.listHotspot.forEach((e,i)=>{
                        console.log('icn',e.icon)
                        if(e.icon=='Custom'){
                            // getBase64FromUrl(e.iconnya).then(dataUrl=>{
                                    infospots[i] = new PANOLENS.Infospot( parseInt(e.size), e.iconnya );
                                    infospots[i].position.set( parseFloat(JSON.parse(e.hotspot).x), parseFloat(JSON.parse(e.hotspot).y), parseFloat(JSON.parse(e.hotspot).z) );
                                    infospots[i].addHoverText( e.title );
                                    infospots[i].addEventListener( 'click', function(){
                                        if(e.type=="Text"){
                                            sdb.alert('Text')
                                            // that.rightSide=!that.rightSide;
                                            // that.dataSide=e;
                                            that.$forceUpdate();
                                        }else if(e.type=="Photo"){
                                            sdb.alert('Photo')
                                            // that.rightSide=!that.rightSide;
                                            // that.dataSide=e;
                                            that.$forceUpdate();
                                        }else if(e.type=="Video"){
                                            sdb.alert('Video')
                                            // that.rightSide=!that.rightSide;
                                            // that.dataSide=e;
                                            that.$forceUpdate();
                                        }else if(e.type=="Route"){
                                            sdb.alert('Route')
                                            // viewer.setPanorama(that.obj.filter(k=>k.id==e.idroute)[0].panorama);
                                            // setTimeout(() => {
                                            //     viewer.tweenControlCenter( new THREE.Vector3( parseFloat(JSON.parse(e.initialRoute).x), parseFloat(JSON.parse(e.initialRoute).y), parseFloat(JSON.parse(e.initialRoute).z)), 0 );
                                            // }, 200);
                                        }
                                    });
                                    panorama.add(infospots[i]);
                                // })
                        }else{
                            if(e.icon=="Arrow"){
                                infospots[i] = new PANOLENS.Infospot( parseInt(e.size), PANOLENS.DataImage.Arrow );
                                // infospots[i] = new PANOLENS.Infospot( parseInt(e.size), PANOLENS.DataImage.Info );
                            }else if(e.icon=="Info"){
                                infospots[i] = new PANOLENS.Infospot( parseInt(e.size), PANOLENS.DataImage.Info );
                            }
                            infospots[i].position.set( parseFloat(JSON.parse(e.hotspot).x), parseFloat(JSON.parse(e.hotspot).y), parseFloat(JSON.parse(e.hotspot).z) );
                            infospots[i].addHoverText( e.title );
                            infospots[i].addEventListener( 'click', function(){
                                if(e.type=="Text"){
                                    sdb.alert('Text')
                                    // that.rightSide=!that.rightSide;
                                    // that.dataSide=e;
                                    that.$forceUpdate();
                                }else if(e.type=="Photo"){
                                    sdb.alert('Photo')
                                    // that.rightSide=!that.rightSide;
                                    // that.dataSide=e;
                                    that.$forceUpdate();
                                }else if(e.type=="Video"){
                                    sdb.alert('Video')
                                    // that.rightSide=!that.rightSide;
                                    // that.dataSide=e;
                                    that.$forceUpdate();
                                }else if(e.type=="Route"){
                                    sdb.alert('Route')
                                    // viewer.setPanorama(that.obj.filter(k=>k.id==e.idroute)[0].panorama);
                                    // setTimeout(() => {
                                    //     viewer.tweenControlCenter( new THREE.Vector3( parseFloat(JSON.parse(e.initialRoute).x), parseFloat(JSON.parse(e.initialRoute).y), parseFloat(JSON.parse(e.initialRoute).z)), 0 );
                                    // }, 200);
                                }
                            });
                                panorama.add(infospots[i]);
                            // that.obj.filter(k=>k.id==e.idgambar)[0].panorama.add(infospots[i]);
                        }
                    })

                        viewer.add( panorama, panorama2 );
                        console.log(viewer)

                        viewer.addUpdateCallback(function(){
                        
                        });
                }, 500);
                this.$forceUpdate();
            });
        }
    },
    mounted() {
        let that=this;
        this.getData();
        this.getDataAsli();
        this.inits();
        this.getListPhoto();
    },
    // metaInfo: {
    //     script: [
    //         {
    //             src: "https://gis.egov.balangankab.go.id/files/three.min.js"
    //         },
    //         {
    //             src: "https://gis.egov.balangankab.go.id/files/panolens.js"
    //         },
    //     ]
    // }
}
</script>